* {
  font-family: 'Courier New', Courier, monospace;
}

body {
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  border: 1px solid gray;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 200px;
}